/* Index Gallery */
/* ============================================================= */
/* ============================================================= */

.Index-gallery-item-image, .tweak-index-gallery-layout-slideshow .Index-gallery-wrapper,
figure,
.Intro--has-image
{
	background-color: transparent !important;
}

.Index-gallery figure.Index-gallery-item-image::before {
    content: ' ';
    display: block;
    position: absolute;
    width: 100%;
    background: gray;
    z-index: 998;
    height: 14rem;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(0,0,0,0) 5%,rgba(0,0,0,0.70) 100%);
}

